.headerContainer {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #d6dadc;
    margin-bottom: 16px;
    justify-content: space-between;
    
}
.menu {
    background-image: url(./assets/ic-menu-24.svg);
    background-repeat: no-repeat;
    background-size:contain;
    display: inline-block;
    width:24px;
    height: 24px;
}
.logo {
    background-image: url(./assets/logo120.svg);
    display: inline-block;
    width: 120px;
    height: 48px;
}
.msg {
    background-image: url(./assets/ic-messages-24.svg);
    background-repeat: no-repeat;
    background-size:contain;
    display: inline-block;
    width:24px;
    height: 24px;
}
.iconContainer {
    padding:12px;
}