.menuItemGroup {
    display: flex;
    flex-wrap:wrap;
    margin: 0px 12px;
}

.menuItemGroup:first-child {
    justify-content: space-between;
    margin-top:4px;
}
.menuItemGroup:last-child {
    margin-bottom:4px;
}
hr {
    border:1px solid #F3F3F3;
}

.DinamicMenu {
    box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
    position: fixed;
    top:0px;
    z-index: 10;
    background-color: white;
    width: 100%;
}