* {
    margin: 0px;
    font-weight: normal;
}

.GoodsCompItem {
    display: block;
    padding: 20px 10px;
    border-top: 1px solid #eaeaea;
    background-color: #f3f3f3;
}

h1 {
    font-size: 18px;
    line-height: 20px;
    text-align: left;
    color:#0052c2;

}

h2 {
    color:hsl(0,0%,29%);
    display: inline-block;
    font-size: 18px;
    line-height: 20px;
    text-align: left;
    
}

.listItemH3 {
    margin-top: 8px;
    margin-bottom: 12px;
}

.image1 {
    background-image: url(./image1.jpg);
    display: inline-block;
    background-size:cover;
    width: 184px;
    height: 144px;
    margin-right:14px;
    margin-bottom: 12px;
}
.image2 {
    background-image: url(./image2.png);
    display: inline-block;
    background-size: cover;
    width: 184px;
    height: 122px;
    margin-right:14px;
    margin-bottom: 12px;
}
.image {
    display: inline-block;
    background-size: cover;
    width: 184px;
    min-height: 30px;
    margin-right:14px;
    margin-bottom: 12px;
}
.Group {
    display: flex;

}
h3 {
    color:hsl(0,0%,29%);
    font-size:14px;
    text-align: left;
}
h4 {
    color:hsl(0,0%,47%);
    font-size: 12px;
    line-height: 14px;
    text-align: left;
}
h4+h4 {
    margin-top:4px;
}
span {
    color:hsl(0,0%,47%);
    font-size: 11px;
    line-height: 11px;
    text-align: left;
}
.eye {
    background-image: url(./icCountView.svg);
    display: inline-block;
    width:14px;
    height: 14px;
    margin-left: 12px;
    margin-right: 4px;
}