.arrow {
    background-image: url(./assets/search_glass.png);
    background-repeat: no-repeat;
    background-size:contain;
    display: inline-block;
    width:16px;
    height: 16px;
}

.SearchInputContainer {
    margin-top:20px;
    display: flex;
}


.icon{
    display: inline-block;
    width: 16px;
    height:16px;
    background-image: url(./assets/search_glass.png);

    background-repeat: no-repeat;
    background-size:contain;

}
.SearchInputContainer button {
    padding: 11px;
    background-color: #4ba9f7;
    border-radius: 4px;
    border: none;
    margin-left:24px;
}
.SearchInputContainer input {
    font-size: 16px;
    width: 100%;
    padding: 6px 0 6px 8px;
    border-radius: 3px;
    border: 1px solid #ddd;
}
