:focus{ 
    outline: none;
}

.selectAll button,label {
    display: flex;
    align-items: center;
    padding-left: 12px;
    padding-right: 12px;
    background:none;
    border:none;
    font-size: 14px;
    line-height: 30px;
    color: #0052c2;
    cursor: pointer;
}
.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.singleButton {
    display: flex;
    align-items: center;
    margin-left: 12px;
}

i {
    display: inline-block;
    width: 16px;
    height:16px;
    background-image: url(./assets/settings.png);
    background-repeat: no-repeat;
    background-size:contain;

}